import React, { Component } from "react";
import { BrowserRouter, Route, withRouter } from "react-router-dom";
import { AnimatedSwitch } from "react-router-transition";
import { IntlProvider } from "react-intl";

import { connect } from "react-redux";
import PropTypes from "prop-types";
import msg from "./data/messages";

import Home from "./pages/Home";
import Application from "./pages/Application";
import Global from "./pages/Global";
import Machines from "./pages/Machine";
import Entreprise from "./pages/Entreprise";
import NotFound from "./pages/NotFound";

import "./assets/css/main.css";

class App extends Component {
  render() {
    let { lang } = this.props;
    return (
      <IntlProvider locale={lang} messages={msg[lang]}>
        <BrowserRouter>
          <AnimatedSwitch
            atEnter={{ opacity: 0.5 }}
            atLeave={{ opacity: .5 }}
            atActive={{ opacity: 1 }}
            className="switch-wrapper"
          >
            <Route exact path="/" component={Home} />
            <Route path="/application/:domain" component={Application} />
            <Route path="/:domain/:param" component={Global} />
            <Route path="/machines" component={Machines} />
            <Route path="/entreprise" component={Entreprise} />
            <Route component={NotFound} />
          </AnimatedSwitch>
        </BrowserRouter>
      </IntlProvider>
    );
  }
}

App.propTypes = {
  lang: PropTypes.string.isRequired
};

function mapStateToProps(state) {
  return {
    lang: state.locale.lang
  };
}

export default withRouter(connect(mapStateToProps)(App));



